<template>
  <div id="SFH_OEE">
    <div id="SFH_OEE_header">
      <h2>设备OEE看板</h2>
    </div>
    <div id="search">
      <el-form :inline="true">
        <el-row>
          <el-form-item label="设备编码">
            <el-input size="small"
              placeholder="请输入内容"
              v-model="input"
              clearable>
            </el-input>
          </el-form-item>
          <el-form-item label="起始时间">
            <el-date-picker
              v-model="time"
              type="datetimerange"
              start-placeholder="开始日期"
              end-placeholder="结束日期"
              :default-time="['08:00:00','20:00:00']">
            </el-date-picker>
          </el-form-item>
          <el-button class="el-icon-search" size="small" type="success" round @click = 'search'>搜索</el-button>
        </el-row>
      </el-form>
    </div>
    <hr/>
    <div id="SFH_OEE_body">
      <el-row :gutter="20" id="SFH_OEE_body_top">
        <el-col :span="12">
          <div class="grid-content bg-purple">
            <span class="fon">当前OEE</span>
            <div id="current_oee_chart" ></div>
          </div>
        </el-col>
        <el-col :span="8">
          <div class="grid-content bg-purple">
            <span class="fon">当前损失率</span>
            <el-table
              :data="tableList"
              style="width: 100%">
              <el-table-column
                prop="time_rate"
                label="时间损失率"
                width="">
              </el-table-column>
              <el-table-column
                prop="efficiency_rate"
                label="效率损失率"
                width="">
              </el-table-column>
              <el-table-column
                prop="quality_rate"
                label="质量损失率"
                width="">
              </el-table-column>
            </el-table>
          </div>
        </el-col>
      </el-row>
      <el-row :gutter="20" id="SFH_OEE_body_bottom">
        <el-col :span="20">

          <div class="grid-content bg-purple">
            <span class="fon">最近七天OEE</span>
            <div id="lately_oee_chart" ></div>
          </div>
        </el-col>
      </el-row>
    </div>
  </div>
</template>

<script>
  export default{
    name:'SFHOEE',
    data(){
      return {
        tableList : [{
          time_rate : '10%',
          efficiency_rate : '5%',
          quality_rate : '6%',
          }
        ],
        input: '',
        time : ''
      }
    },
    methods:{
      // 当前OEE数据封装option
      generateCurrentOeeChartOption : function(data) {
      	if (data === undefined) {
      		data = {
      			'goal': 75,
      			'oee': 70
      		};
      	}
      	//let fontFamily = config.oeeChart.fontFamily;
      	//let fontSize = config.oeeChart.fontSize;
      	//let xLabelRotate = config.oeeChart.xLabelRotate;
      	let pointerColor;
      	if (data.oee >= data.goal) {
      		pointerColor = "#30D27C"
      	} else if (data.oee < data.goal - 5) {
      		pointerColor = "#FF1340"
      	} else {
      		pointerColor = '#FFC600'
      	}
      	return {
      		title: [{
      				show: true,
      				text: '目标：' + data.goal + '%',
      				top: 14,
      				left: 56,
      				textStyle: {
      					color: '#30D27C',
      					fontSize: 14,
      					fontFamily: 'Microsoft YaHei',
      				}
      			},
      			{
      				show: true,
      				text: '目标：',
      				top: 14,
      				left: 14,
      				textStyle: {
      					color: '#fff',
      					fontSize: 14,
      					fontFamily: 'Microsoft YaHei',
      				}
      			}
      		],
      		grid: {
      			top: 0,
      			bottom: 0,
      			left: 0,
      			right: 0
      		},
      		tooltip: {
      			formatter: 'OEE : {c}%<br/>目标：' + data.goal + '%'
      		},
      		toolbox: {
      			show: false,
      			feature: {
      				restore: {},
      				saveAsImage: {}
      			}
      		},
      		series: [{
      			name: 'OEE达成',
      			type: 'gauge',
      			radius: '100%',
      			center: ["50%", "50%"],
      			detail: {
      				formatter: '{value}%',
      				color: pointerColor,
      				fontSize: 18,
      				fontWeight: 'bold'
      			},
      			data: [{
      				value: data.oee
      			}],
      			splitLine: {
      				show: false
      			},
      			axisTick: {
      				show: false
      			},
      			itemStyle: {
      				color: '#0B95FF'
      			},
      			axisLine: {
      				show: true,
      				lineStyle: {
      					color: [
      						[data.goal / 100 - 0.05, '#FF1340'],
      						[data.goal / 100, '#FFC600'],
      						[1, '#30D27C']
      					]
      				}
      			},
      			axisLabel: {
      				color: "#0B95FF"
      			}
      		}]
      	};
      },
      // 最近七天数据封装option
      generate7DaysOeeChartOption : function(data){
        if(data == null){
          data = {};
          data.date = ['12-04', '12-05', '12-06', '12-07', '12-08', '12-09', '12-10'];
          data.data = [80, 75, 60, 88, 70, 75, 86];
        }
        var data_date = data.date;
        var data_value = data.data;
        return  {
          tooltip: {
            trigger: 'axis',
            axisPointer: {            // 坐标轴指示器，坐标轴触发有效
                type: 'shadow'        // 默认为直线，可选为：'line' | 'shadow'
            },
          	formatter: function (datas) {
              var res = datas[0].name + '<br/>';
               return res + 'OEE：' + datas[0].value + '%';
             }
          },
            xAxis: {
                type: 'category',
                name: '日期',
                data: data_date
            },
            yAxis: {
                type: 'value',
                name: 'OEE(%)'
            },
            series: [{
                data: data_value,
                type: 'bar'
            }]
        };
      },

      // 渲染当前OEE数据
      renderCurrentData : function(data){
        let current_oee_chart = this.$echarts.init(document.getElementById('current_oee_chart'));
        let currentOeeChartOption = this.generateCurrentOeeChartOption(data);
        current_oee_chart.setOption(currentOeeChartOption);
      },
      // 渲染最近七天OEE数据
      render7daysData : function(data){
        let lately_oee_chart = this.$echarts.init(document.getElementById('lately_oee_chart'));
        let latelyOeeChartOption = this.generate7DaysOeeChartOption(data);
        lately_oee_chart.setOption(latelyOeeChartOption);
      },
      //获取当前数据
      getCurrentData : function(data){
        let url = '/getCurrentData';
        //let url = '/Thingworx/Things/BDFactoryControllerT/Services/QueryAllToJson';
        this.$http.post(url,data).then(function(res){
          console.log(res)
        },function(res){
          console.log(res)
        });
      },
      // 获取最近7天数据
      get7DaysData : function(data){
        let url = '/get7DaysData';
        this.$http.get(url,data).then(function(res){
          console.log(res)
        },function(res){
          console.log(res)
        });
      },
      // 初始化数据查询
      initData : function(data){
        let currentData = this.getCurrentData(data);
        let daysData = this.get7DaysData(data);

        // 渲染
        this.renderCurrentData(currentData);
        this.render7daysData(daysData);
      },
      /*搜索*/
      search: function (event) {
        //console.log(this.time[0]);
        let data = {};
        if(this.input != ''){
          data.device_code = this.input;
        }
        if(this.time != ''){
          data.start_time = this.time[0];
          data.end_time = this.time[1];
        }
        //console.log(data);
        // 获取当前设备数据
        let currentData = this.getCurrentData(data);

        // 渲染
        this.renderCurrentData(currentData);

      }

    },
    mounted:function(){
      this.initData('');

      //  根据窗口大小调整曲线大小
      window.onresize = () => {
        let current_oee_chart = this.$echarts.init(document.getElementById('current_oee_chart'));
        let lately_oee_chart = this.$echarts.init(document.getElementById('lately_oee_chart'));
        // 基于准备好的dom，初始化echarts实例
        current_oee_chart.resize();
        lately_oee_chart.resize();
      };
    }
  }
</script>

<style>
  #SFH_OEE_header{
    height: 35px;
    text-align: center;
  }
  #SFH_OEE_body_top{
    height: 330px;
  }
  #SFH_OEE_body_bottom{
    height: 370px;

  }
  .fon{
    font-weight: bold;
    font-size: 18px;
  }
  #current_oee_chart{
    height: 300px;
    width: auto;
  }
  #lately_oee_chart{
    height: 340px;
    width: auto;
  }
  #search{
    height: 80px;
    line-height: 80px;
  }
  .el-input__inner{
    width: 180px;

  }
  .el-form-item__content{
    line-height: 80px;
  }
</style>



